.App {
  text-align: center;
}

.App-logo {
  height: 80px;
  -webkit-animation: App-logo-origin-spin reverse 6s linear;
          animation: App-logo-origin-spin reverse 6s linear;  
}

.small {
  margin-left: 30px;
}

.large {
  margin-left: 8px;
}

.Lateral-logo:hover {
  -webkit-animation: App-logo-lateral-spin infinite 5s linear;
          animation: App-logo-lateral-spin infinite 5s linear;
}

.Vertical-logo:hover {
  -webkit-animation: App-logo-vertical-spin infinite 5s linear;
          animation: App-logo-vertical-spin infinite 5s linear;
}

.name {
  font-size: 28px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-origin-spin {
  /*from { transform: rotate(); }*/
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes App-logo-origin-spin {
  /*from { transform: rotate(); }*/
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@-webkit-keyframes App-logo-vertical-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-vertical-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes App-logo-lateral-spin {
  from { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  to { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}

@keyframes App-logo-lateral-spin {
  from { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  to { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}



